<template>
  <div class="music-list">
    <div class="music-top">
      <div class="title">发现好歌单</div>
      <button class="more">查看更多</button>
    </div>
    <div class="milist">
      <div class="swiper-container" id="musicSwiper">
        <div class="swiper-wrapper">
          <router-link :to="{path:'/listView',query:{id:item.id}}" class="swiper-slide" v-for="(item, i) in state.musicList" :key="i"> 
            <img :src="item.picUrl" :alt="item.name" />
            <span class="name">{{ item.name }}</span>
            <div class="count">{{ changeValue(item.playCount) }}</div>
          </router-link>
        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination"></div>
      </div>
    </div>
  </div>
</template>

<script>
import "swiper/css/swiper.css";
import Swiper from "swiper";
import { getSongitem } from "../api/index.js";
import {reactive,onMounted,onUpdated} from 'vue'
export default {
    setup(){
        let state = reactive({musicList:[]})
        function changeValue (num){
            let res = 0
            if(num>=100000000){
                 res = num/100000000
                 res = res.toFixed(2) + '亿'
                 
            }else if(num>10000){
                res = num/10000
                res = res.toFixed(2) + '万'
            }
            return res
        }
        onMounted(async ()=>{
            let result = await getSongitem()
            state.musicList = result.data.result;    
        })
        onUpdated(()=>{
            var swiper = new Swiper('#musicSwiper', {
                slidesPerView: 3,
                spaceBetween: 10,
            });
        })
        return {
            state,changeValue
        }
    }
}
/*
export default {
  data() {
    return {
      musicList: [],
    };
  },
  async mounted() {
    let res = await getSongitem(20);
    this.musicList = res.data.result;
  },
  updated(){
    var swiper = new Swiper("#musicSwiper", {
      slidesPerView: 3,
      spaceBetween: 10,
    });
  },
  methods:{
    changeValue(num){
        let res = 0
      if(num>=100000000){
        res = num/100000000
        res = res.toFixed(2) +'亿'
      }else if(num>=10000){
        res = num/10000
        res = res.toFixed(2) + '万'
      }
      return res
    }
  }
};*/
</script>
<style lang=less>
.music-list {
  width: 7.5rem;
}
.music-top {
  display: flex;
  justify-content: space-between;
  margin: 0.5rem 0.4rem;
}

.music-top .title {
  font-size: 0.4rem;
  font-weight: 900;
}
.music-top .more {
  border: 1px solid #ccc;
  padding: 0.02rem;
  border-radius: 0.2rem;
  font-size: 0.24rem;
  background-color: #fff;
  outline: none;
}
.milist {
  width: 7.1rem;
  margin-left: 0.2rem;

  .swiper-container {
    width: 100%;
    height: 3.5rem;
  }
  .swiper-slide {
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
  }
  img {
    width: 100%;
    height: auto;
    border-radius: 0.2rem;
  }
  .name {
    height: 1rem;
    font-size: 0.24rem;
    line-height: 0.4rem;
  }
  .count {
    position: absolute;
    right: 0.1rem;
    top: 0.05rem;
    font-size: 0.15rem;
  }
}
</style>